@import url(https://fonts.googleapis.com/css?family=Gelasio|Oswald&display=swap);
@import url(https://fonts.googleapis.com/css?family=Poppins&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

body {
  font-family: "Oswald", sans-serif;
}
.container {
  text-align: center;
  display: flex;
  flex-direction: column;
  padding: 5rem 8vw 5rem 8vw;
}
.componentExample {
  margin-bottom: 1.5rem;
}

.credit {
  font-size: smaller;
}

.btn {
  color: #fff !important;
  width: 100px;
  margin: 0px 20px 30px 0px;
  text-transform: uppercase;
  text-decoration: none;
  background: #161616;
  padding: 10px;
  border-radius: 5px;
  display: inline-block;
  border: none;
  -webkit-transition: all 0.4s ease 0s;
  transition: all 0.4s ease 0s;
  cursor: pointer;
}

.btn:hover {
  background: #434343;
  -webkit-transition: all 0.4s ease 0s;
  transition: all 0.4s ease 0s;
}

.comp-title {
  text-align: left;
}
@media screen and (min-width: 1000px) {
  .theTitle {
    font-size: 25px !important;
  }
  .comp-title {
    font-size: 5px !important;
  }
}
@media screen and (min-width: 320px) {
  .theTitle {
    font-size: calc(20px + 6 * ((100vw - 320px) / 680));
  }

  .comp-title {
    font-size: calc(18px + 6 * ((100vw - 320px) / 680)) !important;
  }
}

input[type="checkbox"] {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
}

.buttons {
  height: 20vh;
  width: 40vw;
  display: flex;
  justify-content: center !important;
  margin: 10px auto;
}

label {
  height: 40px;
  width: 60px;
  margin: auto;
  border-radius: 10px;
  position: relative;
  color: "white";
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.fa {
  font-size: 25px;
  font-weight: 1000;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -80%);
          transform: translate(-50%, -80%);
  cursor: pointer;
}
label > span {
  font-size: 15px;
  font-family: "Poppins", sans-serif;
  font-weight: 800;
  position: absolute;
  margin-top: 5px;
  left: 50%;
  -webkit-transform: translate(-50%, 80%);
          transform: translate(-50%, 80%);
  cursor: pointer;
}

input[type="checkbox"]:checked + label {
  color: #119600;
  box-shadow: 0 15px 45px rgb(24, 249, 141, 0.2);
}

.code {
  text-align: left;
}

